@import url(/src/test/c1/fonts2.css);
body {
  margin: 0;
  /*font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* roboto-regular - latin */
@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    src: url(/reactjs/static/media/roboto-v20-latin-regular.4be1a572.eot); /* IE9 Compat Modes */
    src: local('Roboto'), local('Roboto-Regular'),
         url(/reactjs/static/media/roboto-v20-latin-regular.4be1a572.eot?#iefix) format('embedded-opentype'), 
         url(/reactjs/static/media/roboto-v20-latin-regular.479970ff.woff2) format('woff2'), 
         url(/reactjs/static/media/roboto-v20-latin-regular.60fa3c06.woff) format('woff'), 
         url(/reactjs/static/media/roboto-v20-latin-regular.329ae1c3.ttf) format('truetype'), 
         url(/reactjs/static/media/roboto-v20-latin-regular.8681f434.svg#Roboto) format('svg'); /* Legacy iOS */
  }



.App {
  text-align: center;
  font-family: Advent Pro , Roboto;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

